.grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  column-gap: 20px;

  @include onMedium {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include onDesktop {
    grid-template-columns: repeat(12,1fr);
    column-gap: 30px;
  }

  &--desktop {
    display: block;

    @include onMedium {
      display: grid;
    }

    @include onDesktop {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }

        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }

        &--tablet-#{$start}-#{$end} {
          @include onMedium {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }

  &__id {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-row: #{$start} / #{$end + 1};
        }

        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
            grid-row: #{$start} / #{$end + 1};
          }
        }

        &--tablet-#{$start}-#{$end} {
          @include onMedium {
            grid-row: #{$start} / #{$end};
          }
        }
      }
    }
  }
}
