.events {
  margin: 0 0 20px;

  &__photo {
    margin-bottom: 20px;

    &--second {
      @include onMedium {
        margin-bottom: 100px;
      }
    }
  }

  &__image {
    width: 280px;
    height: 356px;

    @include onMedium {
      width: 330px;
      height: 420px;
    }

    @include onDesktop {
      width: 100%;
      height: 100%;
    }
  }

  &__time {
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: $main;
    margin: 0 0 10px;
  }

  &__title {
    font-family: inherit;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    color: $black;
    margin: 0 0 20px;

    @include onMedium {
      font-size: 24px;
    }

    &--second-card {
      @include onMedium {
        margin: 0 0 20px;
      }
    }
  }

  &__description {
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #504F5E;

    margin: 0 0 30px;

    @include onMedium {
      margin: 0 0 40px;
    }
  }
}
